
@mixin text-shortener {
    display: block;
    overflow: hidden;

    white-space: nowrap;
    text-overflow: ellipsis;
}

@mixin block-shortener($fz: 1rem, $lh: 1.5, $h: 70px, $l: 3) {
    display: -webkit-box;
    overflow: hidden;

    height: $h;

    font-size: $fz;
    line-height: $fz * $lh;

    -webkit-box-orient: vertical;
    -webkit-line-clamp: $l;
}

@mixin bg-color($a: 0, $rgb: 0) {
    background: rgba($rgb, $rgb, $rgb, $a);
}

@mixin block-header {
    margin: 0;
    padding-bottom: 10px;

    border-bottom: 1px solid $dark-color;
}

@mixin visually-hidden {
    position: absolute;

    overflow: hidden;
    clip: rect(0 0 0 0);

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;

    border: 0;
}